@mixin uw-heading1 {
	font-family: $headingfont;
	font-weight: 900;
	color: $purple;
}

@mixin uw-heading2 {
	font-family: $subheadfont;
	font-weight: 100;
	text-transform: uppercase;
}

@mixin udub-slant-divider {
	// UW Slant
	overflow: hidden;
	display: block;
	margin: 0.5rem 0 1rem;

	span {
		transform: skew(-15deg, 0);
		display: block;
		height: 0.3rem;
		width: 4rem;
		background-color: $purple;
		margin: 0 0 0 -5px;
	}

	&.gold {
		span { background-color: $gold; }
	}

	&.white {
		span { background-color: white;}
	}
}

@mixin shadow-1 {
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.14);
}

@mixin shadow-2 {
	box-shadow: 0 2px 2px rgba(0, 0, 0, 0.14);
}

@mixin shadow-3 {
	box-shadow: 0 3px 4px rgba(0, 0, 0, 0.14);
}

@mixin shadow-4 {
	box-shadow: 0 4px 5px rgba(0, 0, 0, 0.14);
}

@mixin shadow-6 {
	box-shadow: 0 6px 10px rgba(0, 0, 0, 0.14);
}

@mixin shadow-8 {
	box-shadow: 0 8px 10px rgba(0, 0, 0, 0.14);
}

@mixin shadow-9 {
	box-shadow: 0 9px 12px rgba(0, 0, 0, 0.14);
}

// Background sizing
@mixin background-size($size){
  -webkit-background-size: $size;
     -moz-background-size: $size;
       -o-background-size: $size;
          background-size: $size;
}
